import React, {useState} from 'react';
import {AnimatePresence, motion} from 'framer-motion';

type TooltipLinkType = {
    children: React.ReactNode
    href: string
    TooltipContent: React.ElementType
}

export const TooltipLink = ({
                                children,
                                href,
                                TooltipContent,
                            }: TooltipLinkType) => {

    const [open, setOpen] = useState(false);

    const showTooltip = TooltipContent && open;

    return (
        <div
            onMouseEnter={() => setOpen(true)}
            onMouseLeave={() => setOpen(false)}
            className="relative w-fit h-fit"
        >
            <a href={href} className="relative text-black text-3xl">
                {children}
                <span
                    style={{
                        transform: showTooltip ? 'scaleX(1)' : 'scaleX(0)',
                    }}
                    className="absolute -bottom-2 -left-2 -right-2 h-0.125 origin-left scale-x-0 rounded-full bg-color-gold  transition-transform duration-300 ease-out"
                />
            </a>
            <AnimatePresence>
                {showTooltip && (
                    <motion.div
                        initial={{opacity: 0, y: 15}}
                        animate={{opacity: 1, y: 0}}
                        exit={{opacity: 0, y: 15}}
                        style={{translateX: '-50%'}}
                        transition={{duration: 0.3, ease: 'easeOut'}}
                        className="absolute left-1/2 top-12 bg-white text-black"
                    >
                        <div className="absolute -top-6 left-0 right-0 h-6 bg-transparent"/>
                        <div
                            className="absolute left-1/2 top-0 h-4 w-4 -translate-x-1/2 -translate-y-1/2 rotate-45 bg-white"/>
                        <TooltipContent/>
                    </motion.div>
                )}
            </AnimatePresence>
        </div>
    );
};